*{padding:0;margin:0;box-sizing:border-box;font-family:"Quicksand","Josefin Sans";scroll-behavior:smooth}:root{--mainGold: #c9905a;--goldLowOp: rgba(201, 144, 90, 0.3);--darkGold: #b37842;--hoverGold: #e5b98f;--mainBlue: #1b3c80;--blueLowOp: rgba(27, 60, 128, 0.2);--darkBlue: #0c2558;--hoverBlue: #4f6ead;--mainDark: #232323;--lowOpacityDark: rgba(35, 35, 35, 0.85);--veryLowOpacityDark: rgba(35, 35, 35, 0.4);--mainLight: #efefef;--hoverLight: rgb(221, 221, 221);--lowOpacityLight: rgba(239, 239, 239, 0.85);--shadow: rgba(0, 0, 0, 0.12);--lightShadow: rgba(255, 255, 255, 0.12)}body{min-height:200vh;width:100%;color:#efefef;color:var(--mainLight);background-color:#232323;background-color:var(--mainDark)}button,input[type=submit]{padding:1rem 1.5rem;font-weight:600;border:none;outline:none;cursor:pointer;font-size:1rem;border-radius:2px;transition:color 300ms ease,background-color 300ms ease,background-position 300ms ease,border 300ms ease,box-shadow 500ms ease,padding 300ms ease;font-family:"Josefin Sans";text-transform:uppercase;background-size:150%;background-position:0 center}button.gold.primary,input[type=submit].gold.primary{border:2px solid #c9905a;border:2px solid var(--mainGold);background-image:linear-gradient(45deg, #b37842, #e5b98f);background-image:linear-gradient(45deg, var(--darkGold), var(--hoverGold));color:#efefef;color:var(--mainLight);box-shadow:0px 0px 0px #c9905a;box-shadow:0px 0px 0px var(--mainGold)}button.gold.primary:hover,input[type=submit].gold.primary:hover{box-shadow:0px 0px 4px #c9905a;box-shadow:0px 0px 4px var(--mainGold);background-position:100% center}button.gold.secondary,input[type=submit].gold.secondary{border:2px solid #c9905a;border:2px solid var(--mainGold);color:#c9905a;color:var(--mainGold);background-color:transparent}button.gold.secondary:hover,input[type=submit].gold.secondary:hover{background-color:#c9905a;background-color:var(--mainGold);color:#efefef;color:var(--mainLight)}button.blue.primary,input[type=submit].blue.primary{border:2px solid #1b3c80;border:2px solid var(--mainBlue);background-image:linear-gradient(45deg, #0c2558, #4f6ead);background-image:linear-gradient(45deg, var(--darkBlue), var(--hoverBlue));color:#efefef;color:var(--mainLight)}button.blue.primary:hover,input[type=submit].blue.primary:hover{background-position:100% center}button.blue.secondary,input[type=submit].blue.secondary{border:2px solid #1b3c80;border:2px solid var(--mainBlue);color:#1b3c80;color:var(--mainBlue);background-color:transparent}button.blue.secondary:hover,input[type=submit].blue.secondary:hover{background-color:#1b3c80;background-color:var(--mainBlue);color:#efefef;color:var(--mainLight)}input{border:none;outline:none;font-size:1rem;padding:1rem 1.5rem;border-radius:2px}@media(max-width: 64em){.hide-for-mobile{display:none !important}}@media(min-width: 64.01em){.hide-for-desktop{display:none !important}}
