r}section.mobile-menu .social-media li:not(:last-child){margin-right:.7rem}section.mobile-menu .social-media li a{font-size:1.6rem;text-decoration:none;color:var(--mainBlue);transition:color 400ms ease}section.mobile-menu .social-media li a:hover{color:var(--mainGold)}section.mobile-menu .copyright,section.mobile-menu .links{font-size:.8rem;font-weight:500;margin-top:1rem}section.mobile-menu .copyright a,section.mobile-menu .links a{margin-top:.8rem;text-decoration:none;color:var(--mainBlue);transition:color 400ms ease}section.mobile-menu .copyright a:hover,section.mobile-menu .links a:hover{color:var(--mainGold)}section.mobile-menu .links{margin-top:.5rem}section.mobile-menu .links span{margin:-3px .3rem 0}
