@keyframes breath{0%{transform:scale(1)}50%{transform:scale(1.1)}100%{transform:scale(1)}}.banner{min-height:100vh;width:100%;position:relative;display:flex;align-items:center;justify-content:center;overflow:hidden}.banner .header-effect,.banner .pic{height:100%;width:100%;position:absolute;top:0;left:0;right:0;bottom:0}.banner .header-effect{z-index:-2;background-image:linear-gradient(rgba(0, 0, 0, 0.5), 10%, rgba(0, 0, 0, 0))}.banner .banner-video{position:absolute;z-index:-4;top:0;left:0;right:0;bottom:0;display:flex;align-items:center;justify-content:center}.banner .banner-video video{min-width:100%;min-height:100%;flex-shrink:0}@media(max-width: 37.4375em){.banner .banner-video{display:none}}.banner .pic{z-index:-3;background-size:cover;background-position:center;background-repeat:no-repeat;background-attachment:fixed;opacity:0;transition:opacity 1s ease;animation:breath 20s ease-out infinite forwards}.banner .pic.current{opacity:1}@media(min-width: 37.5em){.banner .pic.video-initialized{display:none;animation:none}}.banner .content{display:flex;align-items:center;justify-content:center;position:absolute;top:0;left:0;right:0;bottom:0}
